import React from "react";
import s from "./AccountLease.module.css";
import aparts from "../../../assets/images/aparts.jpg";
import {useNavigate} from "react-router-dom";


const AccountLease = () => {

    const navigate = useNavigate()
    const handleChangePage = (str) => {
        navigate(str)
    }

    return (
        <div>
            <div className={s.account_main}>
                <div className={s.container}>
                    <div className={s.account_bar}>
                        <div className={s.bar_main} onClick={() => handleChangePage("/login")} role={"button"}></div>
                        <div className={s.bar_lease} onClick={() => handleChangePage("/login/arenda")} role={"button"}>/</div>
                        <div className={s.bar_trip} onClick={() => handleChangePage("/login/trip")} role={"button"}></div>
                        <div className={s.bar_security} onClick={() => handleChangePage("/login/security")} role={"button"}></div>
                    </div>
                    <div className={s.account_data}>
                        <div className={s.account_header}>
                            <div className={s.account_rentapart}>
                                
                            </div>
                            <div className={s.account_passapart}>
                                
                            </div>
                        </div>
                        <div className={s.account_rent}>
                            <div className={s.search_apart}>
                                <div>
                                    <img className={s.search_icon} src={aparts} alt=""/>
                                </div>
                                <div className={s.search_main}>
                                    <div className={s.search_title}>
                                        : Emery Hall
                                    </div>
                                    <div className={s.search_description}>
                                        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;   
                                        BTS Thong Lo sky 300 .   .   
                                          .
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default AccountLease;