import React from "react";
import s from "./AccountSecurity.module.css";
import acc from "../../../assets/images/avatar.jpg"
import {useNavigate} from "react-router-dom";

const AccountSecurity = () => {

    const navigate = useNavigate()
    const handleChangePage = (str) => {
        navigate(str)
    }

    return (
        <div>
            <div className={s.account_main}>
                <div className={s.container}>
                    <div className={s.account_bar}>
                        <div className={s.bar_main} onClick={() => handleChangePage("/login")} role={"button"}></div>
                        <div className={s.bar_lease} onClick={() => handleChangePage("/login/arenda")} role={"button"}>/</div>
                        <div className={s.bar_trip} onClick={() => handleChangePage("/login/trip")} role={"button"}></div>
                        <div className={s.bar_security} onClick={() => handleChangePage("/login/security")} role={"button"}></div>
                    </div>
                    <div className={s.account_data}>
                        <div className={s.account_logo}>
                            <img className={s.account_avatar} src={acc} alt=""/>
                            <div className={s.account_rating}>
                                <div>: 5.0</div>
                                <div>: 1250</div>
                            </div>
                        </div>
                        <div className={s.account_information}>
                            <div> </div>
                            <div className={s.security_newpass}> </div>
                            <div></div>
                            <div className={s.security_comfpass}> </div>
                            <div> </div>
                            <div className={s.security_oldpass}> </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    )
}

export default AccountSecurity;