import React from "react";
import {Link} from "react-router-dom";
import s from "./NavBar.module.css";

const NavBar = ({auth, name, outToggleAuth}) => {


    return (
        <div className={s.header}>
            <div className={s.header_main}>
                <Link className={s.header_main_title} to={"/"}></Link>
                <Link className={s.header_main_title} to={"/search"}></Link>
                <Link className={s.header_main_title} to={"/countries"}></Link>
                <Link className={s.header_main_title} to={"/trip"}></Link>
            </div>

            {/*<Link className={s.header_login} to={"/login"}>*/}
            {/*     */}
            {/*</Link>*/}

            <Link to={"/login"}>
                <div className="btn-group">
                    <button type="button" className="btn btn-primary"> </button>
                    <button type="button" className="btn btn-primary dropdown-toggle dropdown-toggle-split"
                            data-bs-toggle="dropdown" aria-expanded="false">
                        <span className="visually-hidden">  </span>
                    </button>
                    <ul className="dropdown-menu">
                        <Link className={"dropdown-item"} to={"/"}></Link>
                        <Link className={"dropdown-item"} to={"/search"}></Link>
                        <Link className={"dropdown-item"} to={"/countries"}></Link>
                        <Link className={"dropdown-item"} to={"/trip"}></Link>
                        <Link className={"dropdown-item"} to={"/login"}> </Link>
                        <li>
                            <hr className="dropdown-divider"/>
                        </li>
                        <Link className="dropdown-item" to={"/login"} onClick={outToggleAuth}>  </Link>
                    </ul>
                </div>
            </Link>

            {auth && <div>{name}</div>}

        </div>
    );
};

export default NavBar;
